* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Zen Kaku Gothic Antique", serif;
  font-weight: 500;
  font-size: 4.2666666667vw;
  line-height: 1.8;
  color: #5E3516;
}
body.fixed {
  overflow-y: hidden;
}

img {
  display: block;
  width: 100%;
}

a {
  text-decoration: none;
}
a.disabled {
  pointer-events: none;
  opacity: 0.5;
}

ul {
  list-style-type: none;
}

@media print, (min-width: 768px) {
  body {
    font-size: 1.641025641vw;
  }
  a[href*="tel:"] {
    pointer-events: none;
  }
}
@media print, (min-width: 1560px) {
  body {
    font-size: 25.6px;
  }
}
@keyframes pcbg_animation01 {
  0% {
    transform: translate(120%, 0%) rotate(20deg);
  }
  10% {
    transform: translate(0%, 0%) rotate(0deg);
  }
  30% {
    transform: translate(0%, 0%) rotate(0deg);
  }
  35% {
    transform: translate(120%, 0%) rotate(20deg);
  }
  100% {
    transform: translate(120%, 0%) rotate(20deg);
  }
}
@keyframes pcbg_animation02 {
  0% {
    transform: translate(40%, 180%) rotate(45deg);
  }
  10% {
    transform: translate(0%, 0%) rotate(0deg);
  }
  30% {
    transform: translate(0%, 0%) rotate(0deg);
  }
  35% {
    transform: translate(40%, 180%) rotate(45deg);
  }
  100% {
    transform: translate(40%, 180%) rotate(45deg);
  }
}
@keyframes pcbg_animation03 {
  0% {
    transform: translate(105%, 0%) rotate(20deg);
  }
  10% {
    transform: translate(0%, 0%) rotate(0deg);
  }
  30% {
    transform: translate(0%, 0%) rotate(0deg);
  }
  35% {
    transform: translate(105%, 0%) rotate(20deg);
  }
  100% {
    transform: translate(105%, 0%) rotate(20deg);
  }
}
.l-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
}
.l-main {
  margin-top: 16vw;
  position: relative;
}
.l-wrapper__left {
  display: none;
}
.l-wrapper__right {
  background-image: url("../img/common/bg_mask.png");
  background-size: 100%;
  background-position: top center;
  background-repeat: repeat;
}

@media print, (min-width: 768px) {
  .l-main {
    margin-top: 5.1282051282vw;
  }
  .l-wrapper {
    display: flex;
    padding-right: 2.1961538462vw;
  }
  .l-wrapper__left {
    display: block;
    flex-grow: 1;
    position: sticky;
    top: 0;
    height: 100vh;
  }
  .l-wrapper__right {
    width: 38.4615384615vw;
    flex-shrink: 0;
    box-shadow: 0 0 1.8301282051vw rgba(0, 0, 0, 0.25);
    position: relative;
    z-index: 10;
  }
}
@media print, (min-width: 1560px) {
  .l-main {
    margin-top: 80px;
  }
  .l-wrapper {
    width: 1560px;
    margin: 0 auto;
    padding-right: 34.26px;
  }
  .l-wrapper__right {
    width: 600px;
    box-shadow: 0 0 28.55px rgba(0, 0, 0, 0.25);
  }
}
.c-header {
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0 4vw;
  position: relative;
}
.c-header__link {
  display: block;
}
.c-header__logo {
  width: 40vw;
}
.c-header__wrap {
  background: #ffffff;
  width: 100%;
  height: 16vw;
  border-bottom: 0.5333333333vw solid #e60012;
}
.c-contact {
  margin-top: 8vw;
  padding-bottom: 9.3333333333vw;
}
.c-contact + .c-contact {
  margin-top: 0;
  padding-bottom: 0;
}
.c-contact__inner {
  width: 91.4666666667vw;
  margin: 0 auto;
  padding-top: 8vw;
  border-top: 0.2666666667vw solid #ffffff;
}
.c-contact__heading {
  font-weight: 700;
  font-size: 4.8vw;
  line-height: 1.4444444444;
  color: #ffffff;
}
.c-contact__text {
  font-weight: 700;
  color: #ffffff;
  margin-top: 2.6666666667vw;
}
.c-contact__tel {
  display: flex;
  align-items: center;
  gap: 2.1333333333vw;
  margin-top: 2.4vw;
}
.c-contact__tel__icon {
  width: 6.4vw;
}
.c-contact__tel__text {
  font-weight: 700;
  font-size: 4.8vw;
  color: #ffffff;
}
.c-contact__time {
  display: flex;
  gap: 2.1333333333vw;
  margin-top: 2.4vw;
}
.c-contact__time__icon {
  width: 6.4vw;
  padding-top: 0.5333333333vw;
}
.c-contact__time__text01 {
  font-weight: 700;
  color: #ffffff;
}
.c-contact__time__text02 {
  font-weight: 700;
  font-size: 3.7333333333vw;
  line-height: 1.2;
  color: #ffffff;
}
.c-footer {
  background: #ffffff;
  padding-top: 10.6666666667vw;
}
.c-footer__logo {
  display: block;
  width: 18.6666666667vw;
  margin: 0 auto;
}
.c-footer__list {
  display: flex;
  justify-content: center;
  margin-top: 4vw;
  padding-bottom: 4vw;
}
.c-footer__list__item {
  width: 33.33%;
  display: flex;
  justify-content: center;
}
.c-footer__list__text {
  font-size: 3.2vw;
  color: #343a40;
}
.c-footer__copyright {
  font-weight: 700;
  font-size: 2.6666666667vw;
  line-height: 3;
  color: #333333;
  text-align: center;
  background: #f7f7f7;
}
.c-anker {
  position: relative;
}
.c-anker__point {
  position: absolute;
  top: -18.6666666667vw;
}
.c-totop {
  position: fixed;
  bottom: 0;
  right: 0;
  z-index: 100;
  visibility: hidden;
  opacity: 0;
  transition: 0.3s;
}
.c-totop__link {
  display: block;
  width: 18.6666666667vw;
}
.c-totop.is-active {
  visibility: visible;
  opacity: 1;
}
.c-totop.absolute {
  position: absolute;
}

